const botones = document.querySelectorAll('.box-rating__button');
let seleccion = ''
const submit = document.querySelector('.box-rating__submit');

botones.forEach(boton => boton.addEventListener('click', seleccionar));
submit.addEventListener('click',cambiarPagina);

function seleccionar(e) {
    seleccion = e.target.dataset.value;

    let seleccion_anterior = document.querySelector('.box-rating__button--seleccionado');
    
    if (seleccion_anterior) {
        seleccion_anterior.classList.remove('box-rating__button--seleccionado')
    }

    e.target.classList.add('box-rating__button--seleccionado')
}

function cambiarPagina() {
    const paginaAnterior =document.querySelector('#seleccionador');
    const gracias = document.querySelector('#gracias');
    const seleccionado = document.querySelector('.box-rating__seleccion');
    
    if (seleccion !== '') {
        paginaAnterior.classList.add('box-rating__ocultar');
        gracias.classList.remove('box-rating__ocultar');
        seleccionado.innerHTML = `<p>You selected ${seleccion} out of 5</p>`;
    }

    
}

